import { NavLink } from 'react-router-dom';
import './Navigation.css';

const Navigation = () => {
  return (
    <nav className="navigation">
      <NavLink
        to="/"
        className={({ isActive }) => (isActive ? 'active-link' : 'link')}
      >
        Home
      </NavLink>
      <NavLink
        to="/movies"
        className={({ isActive }) => (isActive ? 'active-link' : 'link')}
      >
        Movies
      </NavLink>
    </nav>
  );
};

export default Navigation;
